@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
      @apply scroll-smooth;
      font-family: Poppins, sans-serif;
    }
  }

  @layer utilities {
    .cursor-custom {
      cursor: url('/public/cursor-default.svg') 12 12, auto;
    }
    
    .cursor-custom-click {
      cursor: url('/public/cursor-click.svg') 12 12, auto;
    }
    
    .dark .cursor-custom {
      cursor: url('/public/cursor-default-white.svg') 12 12, auto;
    }
    
    .dark .cursor-custom-click {
      cursor: url('/public/cursor-click-white.svg') 12 12, auto;
    }
  }

  @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');